
import { Vue, Options } from "vue-class-component";
import RepoItem from "@/components/RepoItem.vue";
import Loading from "@/components/Loading.vue";
import { Repository } from "@/types/repository";

@Options({
  components: { RepoItem, Loading },
})
export default class ProjectView extends Vue {
  menu = false;
  repositories: Array<Repository> = [];
  sort = "updated";
  direction = "desc";
  sortFilter = [
    { title: "Create", value: "created" },
    { title: "Update", value: "updated" },
    { title: "Push", value: "pushed" },
    { title: "Full Name", value: "full_name" },
  ];
  attrs = {
    class: "mb-6",
    boilerplate: true,
    elevation: 2,
  };
  isLoading = false;
  async fetch() {
    this.isLoading = true;
    this.repositories = await this.$apiServices.gerRepository(
      this.sort,
      this.direction
    );
    this.isLoading = false;
  }
  toggleSortDirection() {
    this.direction = this.direction == "asc" ? "desc" : "asc";
  }
  created() {
    this.$watch("sort", () => {
      this.menu = false;
      this.fetch();
    });
    this.$watch("direction", () => {
      this.fetch();
    });
  }
  mounted() {
    if (this.repositories.length == 0) {
      this.fetch();
    }
  }
}
